.apology-section {
	position: relative;
	padding: 5rem 0;
	@include max-md {
		padding: 3rem 0;
	}
	.orn-1 {
		position: absolute;
		left: 0;
		top: 0;
		width: 200px;
		transform: translate(-50%, -35%) scaleX(-1);
		@include max-md {
			width: 160px;
		}
	}
	.orn-2 {
		position: absolute;
		bottom: 0;
		right: 0;
		width: 240px;
		transform: translate(30%, 40%);
		@include max-lg {
			width: 220px;
		}
		@include max-md {
			width: 180px;
		}
	}
	.appology-wrap {
		position: relative;
		border-radius: 10px;
		background-color: var(--color-primary);
		padding: 3rem 2rem;
		@include max-md {
			padding: 2rem 1rem;
		}
	}
}
