.gallery-section {
	position: relative;
	padding: 0 0 3rem 0;
	.zoom-gallery {
		width: 100%;
		position: relative;
		display: grid;
		grid-template-columns: auto auto auto auto auto;
		@include max-md {
			padding: 0;
			grid-template-columns: auto auto auto auto;
		}
		&::before {
			position: absolute;
			content: "";
			width: 461px;
			height: 583px;
			left: -50px;
			top: -30px;
			@include max-sm {
				width: 230px;
				height: 290px;
				left: -15px;
			}
		}
		a {
			position: relative;
			background-color: var(--color-secondary);
			&::before {
				position: absolute;
				content: "";
				top: 0;
				right: 0;
				bottom: 0;
				left: 0;
				background-color: transparent;
				z-index: 2;
				transition: all 0.5s ease-in-out;
			}
			&::after {
				position: absolute;
				font-family: "Font Awesome 6 Free";
				top: 50%;
				left: 50%;
				transform: translate(-50%, -50%);
				content: "\2b";
				transition: all 0.4s ease-in-out;
				color: #f4f4f4;
				padding: 2px 8px;
				font-size: 20px;
				border-radius: 1000px;
				font-weight: 700;
				border: solid 2px #f4f4f4;
				opacity: 0;
				z-index: 3;
			}
			&:hover {
				&::before {
					background-color: #0808085d;
				}
				&::after {
					opacity: 1;
				}
			}
			&.grid-lg {
				grid-column: span 2;
			}
		}
		.photo {
			width: 100%;
			height: 270px;
			object-fit: cover;
			position: relative;
			@include max-lg {
				height: 158px;
			}
			@include max-sm {
				height: 107px;
			}
		}
	}
}
