.thank-section {
	position: relative;
	padding: 3rem 0;
	text-align: center;
	color: #ffff;
	.orn-1 {
		position: absolute;
		left: 0;
		bottom: 0;
		width: 160px;
		z-index: 3;
		@include max-lg {
			width: 140px;
		}
		@include max-md {
			width: 100px;
		}
	}
	.orn-2 {
		position: absolute;
		right: 0;
		top: -2rem;
		transform: scaleX(-1);
		width: 160px;
		@include max-lg {
			width: 140px;
		}
		@include max-md {
			width: 100px;
		}
	}
	.image-wrapper {
		position: absolute;
		top: 0;
		left: 0;
		width: 100%;
		height: 100%;
		mask-image: url(../images/mask-header-nospace.png);
		mask-size: 100% 100%;
		mask-position: center;
		mask-repeat: no-repeat;
		&::before {
			position: absolute;
			left: 0;
			top: 0;
			right: 0;
			bottom: 0;
			background-color: rgba($color: #000000, $alpha: 0.3);
			content: "";
		}
		.thank-background {
			width: 100%;
			height: 100%;
			object-fit: cover;
		}
	}
	.thank {
		position: relative;
		text-align: center;
		padding: 5rem 2rem;
		z-index: 3;
		@include max-md {
			border-width: 2px;
			padding: 2rem 0.75rem;
		}
	}
}
.footer {
	padding: 1rem 0;
	position: relative;
	text-align: center;
	small {
		position: relative;
	}
}
