.wishes-section {
	position: relative;
	padding: 5rem 0 0;
	@include max-md {
		padding: 3rem 0 0;
	}
	.orn-1 {
		position: absolute;
		top: 0;
		right: 0;
		width: 200px;
		transform: translate(50%, -30%);
		@include max-lg {
			width: 160px;
		}
		@include max-md {
			width: 140px;
			transform: translate(40%, -30%);
		}
	}
	.image-wrap {
		position: absolute;
		top: 0;
		left: 0;
		width: 100%;
		height: 100%;
		.wishes-background {
			width: 100%;
			height: 100%;
			object-fit: cover;
		}
	}

	.card-form {
		margin-bottom: 0;
		background-color: var(--color-primary);
		position: relative;
		z-index: 3;
		border-radius: 10px;
		&.card {
			border: none;
		}
		.card-body {
			@include min-lg {
				padding: 2rem;
			}
		}
	}

	.wishes-preview {
		margin-top: -10rem;
		padding-top: 10rem;
		padding-bottom: 3rem;
		position: relative;
		color: var(--color-light);
		.image-wrapper {
			position: absolute;
			top: 0;
			left: 0;
			width: 100%;
			height: 100%;
			mask-image: url(../images/mask-section.png);
			mask-size: 100% 100%;
			mask-position: center;
			mask-repeat: no-repeat;
			&::before {
				position: absolute;
				top: 0;
				right: 0;
				bottom: 0;
				left: 0;
				background-color: rgba($color: #000000, $alpha: 0.3);
				content: "";
			}
			.wishes-background {
				width: 100%;
				height: 100%;
				object-fit: cover;
			}
		}
		.card {
			border: none;
			position: relative;
			background-color: transparent;
			.card-body {
				overflow: hidden;
				@include min-lg {
					padding: 2rem;
				}
				.wishes {
					text-align: left;
					height: 23.05rem;
					overflow: auto;
					&::-webkit-scrollbar {
						display: none;
					}
					.text-muted {
						color: rgba($color: #fff, $alpha: 0.8) !important;
					}
					.wish {
						margin-bottom: 1.5rem;
						h6 {
							font-weight: bold;
						}
						h6,
						p {
							margin-bottom: 0;
						}
					}
				}
			}
		}
	}
}
