.couple-section {
	position: relative;
	padding: 5rem 0;
	text-align: center;
	@include max-md {
		padding: 3rem 0;
	}
	.orn-1 {
		position: absolute;
		right: 0;
		top: 5rem;
		width: 120px;
		@include max-md {
			width: 100px;
			top: 15rem;
		}
	}
	.orn-2 {
		position: absolute;
		bottom: 0;
		left: 0;
		width: 160px;
		@include max-xl {
			width: 140px;
		}
		@include max-md {
			width: 100px;
		}
	}
	.and {
		@include min-md {
			position: absolute;
			top: 40%;
			left: 50%;
			transform: translate(-50%, -40%);
		}
		margin: 1rem 0;
		font-weight: 600;
		color: var(--color-dark);
	}
	.couple {
		position: relative;
		.image-wrap {
			// width: 100%;
			margin: auto;
			position: relative;
			margin-bottom: 1.25rem;
			aspect-ratio: 4/6;
			height: 320px;
			@include max-md {
				width: 80%;
				height: auto;
			}
			.couple-image {
				height: 100%;
				object-fit: cover;
				position: relative;
				width: 100%;
				-webkit-mask-image: url(../images/mask-couple.png);
				mask-image: url(../images/mask-couple.png);
				-webkit-mask-size: 100%;
				mask-size: 100%;
				-webkit-mask-position: center;
				mask-position: center;
				-webkit-mask-repeat: no-repeat;
				mask-repeat: no-repeat;
				&.man {
					-webkit-mask-image: url(../images/mask-couple-man.png);
					mask-image: url(../images/mask-couple-man.png);
				}
			}
			.mask-couple {
				position: absolute;
				left: 50%;
				top: 50%;
				width: 101%;
				height: auto;
				&.scaleX-reverse {
					transform: scaleX(-1);
				}
			}
		}
		.couple-parent {
			width: 80%;
			margin: auto;
		}
		.sosmed-wrap {
			position: relative;
			width: 100%;
			.sosmed {
				color: var(--color-dark);
				text-decoration: none;
				small {
					align-items: center;
					gap: 0.25rem;
					display: flex;
					justify-content: center;
				}
			}
			.fab {
				font-size: 18px;
				@include max-md {
					font-size: 16px;
				}
			}
		}
	}
}
