.story-section {
	position: relative;
	padding: 5rem 0;
	@include max-md {
		padding: 3rem 0;
	}
	.orn-1 {
		position: absolute;
		bottom: 0;
		right: 0;
		width: 240px;
		transform: translate(30%, 50%);
		@include max-lg {
			width: 220px;
		}
		@include max-md {
			width: 180px;
		}
	}
	.story-image-abs {
		margin-bottom: -3rem;
		@include min-md {
			position: absolute;
			left: 0;
			margin-bottom: 0;
		}
	}
	.story-description {
		position: relative;
		text-align: center;
		background-color: var(--color-primary);
		padding: 3rem 2rem;
		z-index: 3;
		border-radius: 10px;
		@include max-md {
			padding: 2rem 1.25rem;
		}
	}
	.image-story-wrap {
		position: relative;
		height: 520px;
		width: 100%;
		mask-image: url(../images/mask-header-nospace.png);
		mask-size: 100% 100%;
		mask-position: center;
		mask-repeat: no-repeat;
		@include max-md {
			height: 420px;
		}
		.story-image {
			object-fit: cover;
			width: 100%;
			height: 100%;
		}
	}
}
