.logo-viding {
	height: 2.4rem;
	@include max-md {
		height: 2.25rem;
	}
}

.header-section {
	position: relative;
	height: 100vh;
	width: 100%;
	.orn-1 {
		position: absolute;
		left: 0;
		bottom: 0;
		width: 160px;
		z-index: 3;
		@include max-lg {
			width: 140px;
		}
		@include max-md {
			width: 100px;
		}
	}
	.orn-2 {
		position: absolute;
		right: 0;
		top: 0;
		width: 200px;
		@include max-lg {
			width: 160px;
		}
		@include max-md {
			width: 130px;
		}
	}
	.image-wrapper {
		position: absolute;
		top: 0;
		left: 0;
		width: 100%;
		height: 100%;
		-webkit-mask-image: url(../images/mask-header.png);
		mask-image: url(../images/mask-header.png);
		-webkit-mask-size: 100% 100%;
		mask-size: 100% 100%;
		-webkit-mask-position: center;
		mask-position: center;
		-webkit-mask-repeat: no-repeat;
		mask-repeat: no-repeat;
		@include max-lg {
			mask-size: 100% 110%;
		}
		@include max-md {
			mask-size: 100% 102%;
		}
		&::after {
			position: absolute;
			top: 0;
			right: 0;
			bottom: 0;
			left: 0;
			background: rgba($color: #000000, $alpha: 0.35);
			content: "";
		}
		.background-header {
			width: 100%;
			height: 100%;
			object-fit: cover;
		}
		.mask-header {
			position: absolute;
			top: 0;
			left: 0;
			width: 100%;
			height: auto;
			z-index: 2;
		}
	}
	.header-content {
		position: relative;
		text-align: center;
		padding: 5rem 0 0;
		@include max-lg {
			padding: 8rem 0 0;
		}
		.header {
			text-align: center;
			width: 100%;
			height: 100%;
			display: flex;
			flex-direction: column;
			justify-content: flex-start;
			align-items: center;
			padding: 1.5rem 0 3rem;
			position: relative;
			z-index: 3;
			.header-title {
				color: #fff;
				position: relative;
				.title {
					color: #fff;
				}
			}
		}
	}
}

.envelope-wrap {
	position: fixed;
	z-index: 1300;
	transition: all 1s ease-out;
	background-color: var(--color-main);
	overflow: hidden;
	color: #fff;
	.header-content {
		.header {
			justify-content: space-between;
			.header-title {
				margin-bottom: 0;
				padding: 3rem;
				@include max-md {
					padding: 3rem 0.75rem;
				}
			}
		}
	}
}
